import React, { Component } from 'react';
import '../../style/style.scss';

class Home extends Component {
    constructor() {
        super();
        this.state = {
            greetings: undefined,
            greetings1: undefined,
            greetings3: undefined,
        }
        setTimeout(() => {
            this.setState({ greetings: 'Hogeldin ekirge!' });
        }, 1000);
        setTimeout(() => {
            this.setState({  greetings1: 'Burada olduuna gre kararsz olmalsn. Hadi birlikte kararszl zelim ve harekete geelim.' });
        }, 3000);
        setTimeout(() => {
            this.setState({ greetings2: 'Hangi alan daha ok ilgini ekiyor?' });
        }, 5000);
        setTimeout(() => {
            this.setState({ greetings3: ['Web Uygulamas Gelitirme', 'Mobil Uygulama Gelitirme'] });
        }, 7000);
        setTimeout(() => {
            this.setState({ greetings4: 'ok gzel mobil, gelitirme alannda bir ok frsat yakalayabilirsin.' });
        }, 9000);
        setTimeout(() => {
            this.setState({ greetings5: 'Aada bulunan dillerden hangisini daha iyi kullanyorsun?' });
        }, 11000);
        setTimeout(() => {
            this.setState({ greetings6: ['Javascript', 'Java', 'Swift', 'Kotlin', 'Objective C'] });
        }, 13000);
        setTimeout(() => {
            this.setState({ greetings7: 'Javascript gzel bir tercih. Javascript ile hem ios hem de android iin uygulama yapabileceksin.' });
        }, 16000);
        setTimeout(() => {
            this.setState({ greetings8: 'Son sorum ekirge. Senin iin uygulama performans nemli mi?' });
        }, 18000);
        setTimeout(() => {
            this.setState({ greetings9: ['Evet', 'Hayr'] });
        }, 20000);
    }

    renderMesajItem(message) {
        return (<div className="message">
            <div className="avatar">
                <img
                    className="test"
                    src="https://cdn-images-1.medium.com/fit/c/60/60/1*pLN3R5sML3dcjAvUZDWtOA.png"
                />
            </div>
            <div className="text">
                <div className="tri" />
                {message}
            </div>
        </div>);
    }

    renderChoices(choice){
        return (
            <li className="choice">
                {choice}
            </li>
        );
    }
    render() {
        return (
            <div>
                <h3>Nereden Balamalym?</h3>
                {this.state.greetings && <p>{this.renderMesajItem(this.state.greetings)}</p>}
                {this.state.greetings1 && <p>{this.renderMesajItem(this.state.greetings1)}</p>}
                {this.state.greetings2 && <p>{this.renderMesajItem(this.state.greetings2)}</p>}
                {this.state.greetings3 &&
                    <ul>
                        {
                            this.state.greetings3.map((item) => {
                                return this.renderChoices(item);
                            })
                        }
                    </ul>
                }
                {this.state.greetings4 && <p>{this.renderMesajItem(this.state.greetings4)}</p>}
                {this.state.greetings5 && <p>{this.renderMesajItem(this.state.greetings5)}</p>}
                {this.state.greetings6 &&
                    <ul>
                        {
                            this.state.greetings6.map((item) => {
                                return this.renderChoices(item);
                            })
                        }
                    </ul>
                }
                {this.state.greetings7 && <p>{this.renderMesajItem(this.state.greetings7)}</p>}
                {this.state.greetings8 && <p>{this.renderMesajItem(this.state.greetings8)}</p>}
                {this.state.greetings9 &&
                    <ul>
                        {
                            this.state.greetings9.map((item) => {
                                return this.renderChoices(item);
                            })
                        }
                    </ul>
                }
            </div>
        );
    }
}

Home.propTypes = {
}

export default Home;